      // Utiliser un chemin relatif correct bas sur l'emplacement du composant
      const images = this.importAllImages(require.context('@/assets/image', false, /\.(png|jpe?g|svg)$/));

      // Crer une squence texte-image en associant chaque image  un texte
      const texts = [
        {
          text: "Bonjour mon chri, je voulais te parler... Le monde est vaste et rempli de mystres. Ta sur est partie pour l'aventure il y a plusieurs mois, mais nous n'avons plus de nouvelles d'elle depuis. Je sais que cela t'inquite, mais je suis sre qu'elle est en scurit. Cependant, je pense qu'il est temps pour toi de partir  sa recherche. Tu es prt, n'est-ce pas ? Avant que tu partes, tu devrais aller voir le Professeur Chne. Il pourra t'aider  te prparer pour ton voyage. Prends soin de toi, mon chri.",
          imageName: 'wanda.png'
        },
        {
          text: "Ah, te voil ! Ta mre m'a parl de ta dcision. C'est une grande responsabilit que tu assumes, mais je suis sr que tu es prt.Avant que tu ne partes, je veux te donner un Pokmon. Ce sera ton premier compagnon d'aventure. Choisis bien, car il sera  tes cts tout au long de ton voyage. Ces trois Pokmon sont tous spciaux  leur manire. Prends ton temps pour faire ton choix. Je suis l pour rpondre  tes questions. Une fois que tu auras choisi, ton aventure commencera officiellement. N'oublie pas, c'est dangereux de partir seul. Bonne chance, jeune dresseur !",
          imageName: 'ana.png'
        },

      ];

      // Associer les textes aux images
      return texts.map((item) => {
        return { text: item.text, image: images[item.imageName] };
      });
    },
  },
};
</script>
